@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion {
    padding: v.$separacion 0;
    
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }
   
    button {
        margin: 0 0 1rem 0;
        font-weight: v.$regular;

        &:first-of-type {
            margin-right: 1rem;
        }
    }
}
.ocultar {
    visibility: hidden;
}