@use 'base/variables' as v;
@use 'base/mixins' as m;


.tabs {

    @include m.tablet {
        display: flex;
    }
    
    button {
        border: none;
        border-bottom: 1px solid v.$blanco;
        padding: 1rem;
        margin-bottom: 1rem;
        color: v.$blanco;
        background-color: v.$negro;
        text-transform: uppercase;
        font-weight: v.$bold;
        display: block;
        width: 100%;
        cursor: pointer;
        transition: all 300ms ease;

        &:hover,
        &.actual {
            color: v.$azul;
            border-bottom: 1px solid v.$azul;
        }
    }
}
.seccion-citas {

}

#nueva-cita {
    margin: v.$separacion auto;
}