@use 'base/variables' as v;


.reservas {
    list-style: none;
    padding: 0;
    margin-bottom: 4rem;

    li {
        border-bottom: 1px solid v.$blanco;
        padding: 1rem 0;

        p {
            color: v.$azul;
        }

        span {
            color: v.$blanco
        }
    }
}