@use 'base/variables' as v;

.citas {

    list-style: none;
    padding: 0;

    li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;

        p{
            font-weight: v.$bold;
            color: v.$azul
        }

        span,
        .servicio {
            font-weight: v.$regular;
            color: v.$blanco
        }

        .total {
            font-size: 2.5rem;
        }
    }
}
.formulario {
    label {
        font-size: 2rem;
        font-weight: v.$bold;
    }
}
.servicios {
    @extend .citas;

    .acciones {
        margin: 0;
        
        .boton,
        .boton-eliminar {
            margin: 0;
        }
    }
}

.barra-servicios {
    a {
        border: none;
        border-bottom: 1px solid v.$blanco;
        padding: 1rem;
        margin-bottom: 1rem;
        background-color: v.$negro;
        text-transform: uppercase;
        font-weight: v.$bold;
        display: block;
        width: 100%;
        cursor: pointer;
        transition: all 300ms ease;
    
        &:hover,
        &.actual {
            color: v.$azul;
            border-bottom: 1px solid v.$azul;
        }
        &:hover {
            background-color: v.$negro;
        }
    }
}
